<template>
  <div class="border">
    <!--  -->
    <el-button  type="text" @click="goHome">
    <img src="../assets/img/.png" alt="" />
   </el-button>

    <!--  -->
    <div class="create-questionnaire">
      <div class="usingButton" >
        <img src="@/assets/img/.png" alt="" />
        <p class="bordertext"></p>
      </div>
    </div>

    <!--  -->
      <button class="noUseButton" @click="my">
        <img src="@/assets/img/.png" alt="" />
        <p class="bordertext"></p>
      </button>

    <!--  -->
      <button class="noUseButton" @click="personalOn">
        <img src="@/assets/img/.png" alt="" />
        <p class="bordertext"></p>
      </button>

    <!--  -->
      <button class="noUseButton" @click="recordOn">
        <img src="@/assets/img/.png" alt="" />
        <p class="bordertext"></p>
      </button>


    <!--  -->
    <main>
      <p></p>
      <input type="text" placeholder="" v-model="questionnaireTitle" />
      <button @click="saveQuestionnaire"></button>
    </main>
  </div>
</template>

<script>
import axios from "axios";
import check from "@/components/MyQuestionnaire.vue"
export default {
  props:['recordOn','personalOn','my','goHome','userId'],
  data() {
    return {
      questionnaireTitle: '', // 
    };
  },
  methods: {
    async saveQuestionnaire() {
      if(!await check.methods.checkUser(this.userId))
      {
        this.goHome();
        return;
      }
      // 
      if (this.questionnaireTitle.trim() !== '') {
        console.log(':', this.questionnaireTitle);
        const response = await axios.post(window.Ip+'/save',{
          "theme":this.questionnaireTitle,
          "creatorId":this.userId
        });
        console.log(response);
        this.my();
      } else {
        alert('');
      }
    },
  },
};
</script>

